/* Basic config for all custom icons: */
.icon {
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center center;
}

.logo {
  background-image: url(../img/svg/Logo.svg);
  width: 49px;
  height: 49px;
}

.menu-toggle {
  background-image: url(../img/svg/Bulk/Menu-toggle-white.svg);
  width: 24px;
  height: 24px;
}

.home {
  background-image: url(../img/svg/Bulk/Home-white.svg);
  width: 24px;
  height: 24px;
}

.document {
  background-image: url(../img/svg/Bulk/Document-white.svg);
  width: 24px;
  height: 24px;
}

.folder {
  background-image: url(../img/svg/Bulk/Folder-white.svg);
  width: 24px;
  height: 24px;
}

.arrow-down {
  background-image: url(../img/svg/Bulk/Arrow-DownCircle.svg);
  width: 24px;
  height: 24px;
}

.image {
  background-image: url(../img/svg/Bulk/Image-white.svg);
  width: 24px;
  height: 24px;
}

.paper {
  width: 24px;
  height: 24px;
  background-image: url(../img/svg/Bulk/Paper-white.svg);
}

.message {
  background-image: url(../img/svg/Bulk/Message-white.svg);
  width: 24px;
  height: 24px;
}

.edit {
  background-image: url(../img/svg/Bulk/Edit-white.svg);
  width: 24px;
  height: 24px;
}

.category {
  background-image: url(../img/svg/Bulk/Category-white.svg);
  width: 24px;
  height: 24px;
}

.user-3 {
  background-image: url(../img/svg/Bulk/3-User-white.svg);
  width: 24px;
  height: 24px;
}

.setting {
  background-image: url(../img/svg/Bulk/Setting-white.svg);
  width: 24px;
  height: 24px;
}

.avatar {
  background-image: url(../img/svg/Bulk/Avatar.svg);
  width: 56px;
  height: 56px;
}

h1,
h2,
h3 {
  letter-spacing: 1px;
  color: #171717;
}

h1 {
  font-size: 64px;
  line-height: 1.03;
}

h2 {
  font-size: 48px;
  line-height: 1.04;
}

h3 {
  font-size: 32px;
  line-height: 1.06;
}

button {
  cursor: pointer;
}

.transparent-btn {
  background-color: transparent;
  border-width: 0;
}

.sidebar-toggle {
  opacity: 0.6;
  transition: 0.3s all;
}

.sidebar-toggle:hover {
  opacity: 1;
}

.category__btn {
  position: absolute;
  border-radius: 50%;
  right: 0;
  top: 13px;
  z-index: 1;
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #0061f7 !important;
  width: 253px;
  padding: 20px 15px 37px 20px;
  transition: 0.3s all;
  /* sidebar head */
  /* sidebar body */
}

.sidebar.hidden {
  width: 64px;
  padding-right: 4px;
  padding-left: 4px;
}

.sidebar.hidden .logo-wrapper {
  display: none;
}

.sidebar.hidden * {
  font-size: 0px;
}

.sidebar.hidden .sidebar-head {
  justify-content: center;
  margin-top: 20px;
  padding-bottom: 40px;
}

.sidebar.hidden .category__btn {
  display: none;
}

.sidebar.hidden .sidebar-body-menu a {
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.sidebar.hidden .sidebar-body-menu a::after {
  right: -4px;
}

.sidebar.hidden .msg-counter {
  right: 10px;
  top: 30%;
}

.sidebar.hidden .icon {
  margin-right: 0;
}

.sidebar.hidden .sidebar-user {
  width: 52px;
  height: 53px;
}

.sidebar.hidden .sidebar-user .icon {
  background-size: contain;
  height: 100%;
  width: 100%;
}

.sidebar-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-body {
  padding-top: 20px;
}

.sidebar-body-menu li {
  position: relative;
}

.sidebar-body-menu a {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 8px 0;
  opacity: 0.6;
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  line-height: 2.43;
}

.sidebar-body-menu a::after {
  position: absolute;
  content: '';
  height: 22px;
  width: 3px;
  right: -11px;
  border-radius: 10px;
  background-color: #fff;
  opacity: 0;
  transition: 0.3s all;
}

.sidebar-body-menu a:hover {
  opacity: 1;
}

.sidebar-body-menu a:hover::after {
  opacity: 1;
}

.sidebar-body-menu a.active {
  opacity: 1;
}

.sidebar-body-menu a.active::after {
  opacity: 1;
}

.sidebar-body-menu a > .icon {
  margin-right: 10px;
}

.sidebar-user {
  border-radius: 10px;
  padding: 5px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background-color: rgba(255, 255, 255, 0.1);
}

.sidebar-user .icon {
  margin-right: 8px;
}

.sidebar-user-info {
  display: flex;
  flex-direction: column;
}

.sidebar-user__title {
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.75px;
  color: #FFFFFF;
}

.sidebar-user__subtitle {
  font-weight: 500;
  font-size: 10px;
  line-height: 1.5;
  letter-spacing: 0.86px;
  color: rgba(255, 255, 255, 0.5);
}

.logo {
  margin-right: 9px;
}

.logo-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.logo-text {
  display: flex;
  flex-direction: column;
  color: #fff;
}

.logo-title {
  font-size: 21px;
  font-weight: 700;
  line-height: 1.4;
  letter-spacing: 0.86px;
}

.logo-subtitle {
  font-size: 12px;
  font-weight: 500;
  line-height: 2.42;
  letter-spacing: 0.86px;
}

.msg-counter {
  position: absolute;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  background-color: #f26464;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-weight: 500;
  font-size: 12px !important;
  line-height: 1.25;
  letter-spacing: 0.75px;
  color: #FFFFFF;
}

.system-menu__title {
  display: inline-block;
  margin-top: 13px;
  margin-bottom: 2px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.75px;
  color: rgba(255, 255, 255, 0.5);
}

.cat-sub-menu {
  padding-left: 34px;
  height: 0;
}

.cat-sub-menu.visible {
  height: auto;
}

.cat-sub-menu.visible a {
  opacity: 1;
  visibility: visible;
}

.cat-sub-menu a {
  opacity: 0;
  visibility: hidden;
}

.category__btn .icon {
  transition: 0.3s;
}

.category__btn.rotated .icon {
  transform: rotate(180deg);
}

.main-nav {
  padding: 20px;
}
